input[type=checkbox] {
	visibility: hidden;
}

input[type=radio], input[type=file] {
	display: none;
}

input:checked + label div {
	border: 1px solid $red-bg;
}

input.valid {
	border-bottom: 2px solid $red-bg;
}

input.invalid {
	border-bottom: 2px solid $yellow;
}

.code-submit {
	text-align: center;
}

.code-input {
	outline: none;
	background: #FFFFFF;
	box-shadow: 0 2px 4px 0 rgba(0,0,0,0.11), inset 0 1px 3px 0 rgba(0,0,0,0.50);
	border-radius: 8px;
	width: 54px;
	height: 54px;
	font-size: 24px;
	text-align: center;
}

.forma-solicitud .panel {
	-webkit-box-shadow: none;
	box-shadow: none;
}

.checkboxForm {
	width: 100%;
	margin: 51px auto;
	position: relative;

	label {
		cursor: pointer;
		position: absolute;
		width: 25px;
		height: 25px;
		top: 0;
		left: 0;
		background: #fff;
		border: 1px solid $blue-green;

		&:after {
			opacity: 0.2;
			content: '';
			position: absolute;
			width: 13px;
			height: 7px;
			background: transparent;
			top: 7px;
			left: 6px;
			border: 2px solid $blue-green;
			border-top: none;
			border-right: none;
			transform: rotate(-45deg);
		}
	}

	span {
		padding-left: 10px;
	}
}
/**
 * Create the hover event of the tick
 */
.checkboxForm label:hover::after {
	opacity: 0.5;
}
/**
 * Create the checkbox state for the tick
 */
.checkboxForm input[type=checkbox]:checked + label:after {
	opacity: 1;
}

#detalles-solicitud .form-group {
	margin-top: 0;
}

select.form-control {
	display: inline-block;
	border: 0;
  box-shadow: 0px 3px 4px #e8e8e8;
}

.filter-bar .form-group span {
	display: inline-block;
}

.form-inline .form-group {
    vertical-align: inherit;
}

.date-search {

    margin: 0 20px;
}
.check-solicitud, .check-all {
	visibility: visible !important;
}

.checkbox {
	position: absolute;
	right: 48px;
	top: 30px;
}
