a:hover .icon-item {
  border: 1px solid $header-bg;
}

.icon-item {
  box-shadow: 0 2px 3px #e8e8e8;
  border-radius: 5px;
  padding: 15px 10px;
  margin: 0 -10px;
  text-align: center;
}

.icon-item img {
  height: 36px;
}

.icon-item.active {
  border: 1px solid $header-bg;
  box-shadow: none;
}

.credit-text {
  text-align: center;
  margin-top: 5px;
}

.form-control:focus,
input.form-control {
  /* Remove First */
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-box-shadow: 0 2px 2px #888;
  box-shadow: 0 2px 2px #888;
  border: 0;
}

.form-group {
  margin-bottom: 15px;
  margin-top: 20px;
}

.footer .icon-menu {
  flex: 1;
}

.footer .icon-menu img {
  height: 36px;
}

svg.no {
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -ms--transform: rotate(180deg);
  transform: rotate(180deg);
}

svg.no path {
  fill: $red-bg !important;
}

svg.white path {
  fill: #fff !important;
}

label.icon-label {
  text-align: center;
}

label.icon-label span {
  padding-left: 20px;
}
