.table > thead > tr > th {
  vertical-align: bottom;
  color: #9B9B9B;
  border: 0;
  font-size: 14px;
  padding: 0 0 8px 8px;
}

.table > tbody > tr > td {
  border: 0;
  font-size: 14px;
  color: #2B3B46;
}

td a {
  color: #6D6D6D;
}

td a:hover {
  color: $red-bg;
}

.right {
  text-align: right;
}
