.panel-heading a{
  display: none;
}

.panel-default {
  border: 0;
}

.panel-default>.panel-heading {
    background-color: transparent;
    border:0;
    text-align: center;
}

.panel-default>.panel-heading+.panel-collapse>.panel-body {
    border: 0;
}

.panel.section-panel {
  margin-top: 20px;
  background-color: #fff;
  box-shadow: 0 4px 9px 0;

  border-radius: 16px;
}

.panel.section-panel img {
  padding: 15px 0;
}

span.mobile-only {
  font-size: 20px;
    line-height: 24px;
}

a[aria-expanded="false"] + span{
  display: none;
}

.swiper-slide .panel {
  background: #fff;
}

.swiper-slide .panel p {
  margin: 7px 0 0 0;

}

.credit-proposals-wrapper .panel .panel-header span {
	display: block;
	margin: 8px 0;
}

.credit-proposals-wrapper img {
	width: 13%;
}

.credit-proposals-wrapper .panel {
	border-radius: 18px;
}

.credit-proposals-wrapper .panel .panel-header {
	border-radius: 15px;
	color: #fff;
	text-align: center;
	background-color: #333;
	padding: 15px;
}

#credit-proposals ul {
	list-style-type: none;
}

.credit-proposals-wrapper .panel.active .panel-header {
	background-color: $blue-green;
}

.credit-proposals-wrapper .panel.panel-red .panel-header {
	background-color: $red-bg;
}

.credit-proposals-wrapper .panel.panel-green .panel-header {
	background-color: $green-bg;
}

.credit-proposals-wrapper .panel.panel-dark .panel-header {
	background-color: #7d7a7a;
}

.credit-proposals-wrapper .panel .panel-body .list-title {
	font-weight: 800;
	font-size: 16px;
}

.credit-proposals-wrapper .panel .panel-body .list-item {
	color: $blue-green;
	font-weight: 500;
  line-height: 20px
}

.credit-proposals-wrapper .panel .panel-body .list-item-percentage {
  display: block;
}

.home .panel-heading {
  border-bottom: 1px solid #ddd;
}

.home .panel-heading a {
  float: right;
  color: #6D6D6D;
}

.home .panel-heading a:hover {
  color: $red-bg;
}

.dummy-document {
  height: 300px;
  background-color: #ddd;
  border-radius: 8px;
}

.dummy-document i {
  color: white;
  padding: 40%;
}

.doc-info {
  padding: 20px 0px 3px 0;
  box-shadow: 0 2px 5px 0;
  margin: 12px 0;
  text-align: center;
  background-color: #fff;
}

.doc-info i{
  color: $red-bg;
}

.doc-name {
  font-weight: 600;
  margin: 14px 0 0 0;
  color: #666;
}

.doc-date {
  font-size: 12px;
  font-style: italic;
  color: #ddd;
}
