.title-section {
	font-size: 20px;
	font-weight: 700;
	text-align: center;
}

h2.credit-form-title {
	font-size: 16px;
	padding-top: 10px;
}

.form-group span {
	display: block;
	font-size: 11px;
	padding-left: 5px;
	line-height: 25px;
}

span.input-success {
	color: $red-bg;
}

h3.section-header {
	font-size: 20px;
	padding-bottom: 15px;
	border-bottom: 1px solid $red-bg;
}

h3.section-header-home {
	font-size: 20px;
	padding: 15px;
	border-left: 1px solid $red-bg;
}

.section-list-title {
	font-size: 12px;
	color: #2B3B46;
	line-height: 16px;
	text-transform: uppercase;
  margin: 10px 0;
}

.section-list-item {
	font-size: 18px;
	color: $red-bg;
	line-height: 24px;
}

h3.section-header-home span {
	font-size: 15px;
  color: $red-bg;
  font-weight: 700;
}

h5.status {
	margin: 45px 0 70px 0;
	text-decoration: underline;
	font-weight: 800;
}
