@import 'partials/variables';
@import 'partials/fonts';
@import 'partials/nav';
@import 'partials/side_menu';
@import 'partials/icons';
@import 'partials/forms';
@import 'partials/buttons';
@import 'partials/panels';
@import 'partials/slider';
@import 'partials/loader';
@import 'partials/editable';
@import 'partials/lists';
@import 'partials/tables';

body {
	background-color: #fff;
	font-family: 'Raleway', sans-serif;
	color: $grey;
	position: relative;
	margin: 0;
	padding-bottom: 6rem;
	min-height: 100%;
}

.container {
	padding-top: 80px;
}

.footer {
	position: fixed;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 1rem;
	border-top: 2px solid #e8e8e8;
	text-align: center;
	display: flex;
	z-index: 5;
	background-color: #fff;
}

.center {
	text-align: center;
}

.nav-btn-back {
	color: $blue-green;
	font-size: 1.2em;
}

.nav-btn-back i {
	padding-right: 6px;
	font-size: 1.3em;
	font-weight: 100;
}

.add-on input {
	text-align: center;
	width: 100%;
}

.add-on .input-group-btn {
	position: absolute;
}

.add-on .btn-default {
	border-color: transparent !important;
}

.add-on .btn-default:hover {
	background-color: #fff;
	cursor: pointer;
}

.add-on .input-group-btn.left {
	top: 4px;
	border: 0;
	left: 0;
	z-index: 4;
}

.add-on .input-group-btn.left i {
	color: red;
}

.add-on .input-group-btn.right i {
	color: $blue-green;
}

.add-on .input-group-btn.right {
	top: 4px;
	border: 0;
	right: 35px !important;
	z-index: 4;
}
/* stop the glowing blue shadow */
.add-on .form-control:focus {
	border-color: #cccccc;
}

.form-control[disabled],
.form-control[readonly] {
	background-color: #fff;
}

.credit-proposals-wrapper {
	padding-top: 25px;
}

#pre-autorizacion p {
	margin: 32px 0;
}

.img-title {
	padding-top: 25px;
}

.folio-aut {
	padding-bottom: 25px;
}

.folio-aut img {
	padding-left: 10px;
}

.denegado .title-section {
	padding-bottom: 64px;
}

.dropzone {
	border: 1px dashed $red-bg;
	min-height: 100px;
	border-radius: 8px;
}

.dropzone .dz-message span i {
	position: absolute;
	top: 10px;
	right: 23px;
	font-size: 2em;
	color: $red-bg;
}

.profile-pic {
	height: 80px;
	position: absolute;
	margin: 0 auto;
	border-radius: 50%;
	padding: 0 !important;
	top:-50px;
	left: 35%;
}

#photo-label {
	position: absolute;
  right: 30px;
  top: -2px;
}

.modal {
	margin: 0 auto;
}

.filter-bar {
	padding-top: 10px;
}


/* importamos responsive al final */
@import 'partials/responsive';
